<template>
  <div id="app">
    <Navbar 
      id="app-nav"
      :selectHome="switchToHome"
      :selectAbout="switchToAbout"
      :selectContact="switchToContact"
      :homeStatus="appHome"
      :aboutStatus="appAbout"
      :contactStatus="appContact"
    />

    <div id="app-body">
      <Home id="app-home" v-if="appHome"/>
      <About id="app-about" v-if="appAbout" />
      <Contact id="app-contact" v-if="appContact" />
    </div>
    
  </div>
</template>

<script>
import Navbar from './components/Navbar';
import Home from './components/Home';
import About from './components/About';
import Contact from './components/Contact';

export default {
  name: 'App',
  data () {
    return {
      appHome: false,
      appAbout: true,
      appContact: false
    }
  },
  methods: {
    switchToHome(){
      this.hideAll();
      this.appHome = true;     
    },
    switchToAbout(){
      this.hideAll();
      this.appAbout = true;
    },
    switchToContact(){
      this.hideAll();
      this.appContact = true;
    },
    hideAll(){
      this.appHome = false;
      this.appAbout = false;
      this.appContact = false;
    }
  },
  components: {
    Navbar,
    Home,
    About,
    Contact
  }
  
  
}
</script>

<style>

  body{
    padding: 0;
    margin: 0;
    font-family: 'Ubuntu', sans-serif;
  }

  #app{
    display: grid;
    grid-template-columns: 1fr 4fr;
    max-width: 100vw;
    margin: 0 auto;
    grid-template-areas: "app-nav app-body"
  }

  #app-nav{
    grid-area: 'app-nav';
    width: 100%;
    height: 100vh;
    text-align: center;
    box-sizing: border-box;
    border-right: 2px solid rgba(0, 89, 255, 0.13);
  }
  
  #app-body{
    grid-area: "app-body";
    background: linear-gradient(to bottom right, rgb(254, 255, 255) , rgb(137, 212, 255));
  }


  @media only screen and (max-width: 600px){

    @viewport{
      height: 100%;
    }

    body{
      margin: 0;
      padding: 0;
      
    }

    #app{
      display: inline-block;
      margin:0;
      padding:0;
      grid-template-areas: none;
      height: auto;
      /* height:100vh;width:100vw; */
    }

    #app-body{
      margin:0;
      padding:0;
      height: 100%;
    }

    #app-nav{
      /* display: none; */
      margin: 0;
      padding: 0;
      height: 100%;
      width: 100vw;
      /* visibility: hidden; */
    }

  }
</style>



